"use client"

import { useState, useEffect } from "react"
import { Card } from "../../components/ui/card"
import { Button } from "../../components/ui/button"

type FormData = {
  [key: string]: string
}

export default function RecordsPage() {
  const [records, setRecords] = useState<FormData[]>([])

  useEffect(() => {
    // Load records from localStorage or initialize empty
    const savedRecords = localStorage.getItem("submittedRecords")
    if (savedRecords) {
      setRecords(JSON.parse(savedRecords))
    }
  }, [])

  return (
    <main className="min-h-screen bg-gray-50">
      <div className="bg-black text-white py-16 mb-8">
        <div className="container mx-auto px-4 text-center">
          <h1 className="text-5xl font-bold mb-4">Commonwealth Food Manufacturing</h1>
          <p className="text-xl text-gray-300">All Submitted Records</p>
        </div>
      </div>
      <div className="container mx-auto px-4 pb-16 max-w-5xl">
        <Card className="p-6 shadow-lg overflow-x-auto">
          {records.length === 0 ? (
            <p>No records found.</p>
          ) : (
            <div className="space-y-6 max-h-[600px] overflow-y-auto">
              {records.map((record, index) => (
                <div key={index} className="border p-4 rounded shadow-sm">
                  <h3 className="font-semibold mb-2">Record #{index + 1}</h3>
                  <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                    {Object.entries(record).map(([key, value]) => (
                      <div key={key}>
                        <p className="font-semibold capitalize">{key.replace(/([A-Z])/g, ' $1')}</p>
                        <p>{value}</p>
                      </div>
                    ))}
                  </div>
                </div>
              ))}
            </div>
          )}
          <div className="mt-6">
            <Button onClick={() => window.history.back()} className="bg-black hover:bg-gray-800 text-white">
              Back to Form
            </Button>
          </div>
        </Card>
      </div>
    </main>
  )
}
